#logo {
    // width: 100%;
    max-width: 12rem;
    height: 2rem;

    svg {
        #nome {
           fill: #000000;
        }

        #background-splash {
            fill: #ffd500;
            opacity: 0;
            transition: .2s;
        }

        #riga {
            fill: #af0303;
            opacity: 0;
        }
    }

    &:hover svg #background-splash {
        opacity: 1;
    }

    &.home {
        svg #riga {
            opacity: 1;
        }
        
        &:hover svg #background-splash {
                opacity: 0;
            }
        }
}

a {
    text-decoration: underline;
    text-decoration-thickness: 0.1rem;

    &:visited {
        color: $link;
    }
}